import React, { useState } from 'react';
import {
  FormControl,
  FormLabel,
  Input,
  Button,
  VStack,
  HStack,
  Checkbox,
  useToast,
  Progress,
  Text,
} from '@chakra-ui/react';
import ScannerLayout from '../common/ScannerLayout';

const WebScanner: React.FC = () => {
  const [target, setTarget] = useState('');
  const [isScanning, setIsScanning] = useState(false);
  const toast = useToast();

  const handleScan = async () => {
    if (!target) {
      toast({
        title: 'Error',
        description: 'Please enter a target URL',
        status: 'error',
        duration: 3000,
      });
      return;
    }

    setIsScanning(true);
    try {
      const response = await fetch('http://localhost:8000/scan/web', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify({ target, options: {} }),
      });
      if (!response.ok) throw new Error('Scan failed');
      toast({
        title: 'Success',
        description: 'Scan started successfully',
        status: 'success',
        duration: 3000,
      });
    } catch (error) {
      toast({
        title: 'Error',
        description: 'Failed to start scan',
        status: 'error',
        duration: 3000,
      });
    }
    setIsScanning(false);
  };

  return (
    <ScannerLayout
      title="Web Vulnerability Scanner"
      description="Scan web applications for security vulnerabilities using AI-powered detection"
    >
      <VStack spacing={6}>
        <FormControl>
          <FormLabel>Target URL</FormLabel>
          <Input
            placeholder="https://example.com"
            value={target}
            onChange={(e) => setTarget(e.target.value)}
          />
        </FormControl>
        <Button
          colorScheme="blue"
          onClick={handleScan}
          isLoading={isScanning}
          loadingText="Scanning"
          w="100%"
        >
          Start Scan
        </Button>
      </VStack>
    </ScannerLayout>
  );
};

export default WebScanner;
