import React from 'react';
import { ChakraProvider, Box, Container } from '@chakra-ui/react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Navbar from './components/Navbar';
import WebScanner from './components/scanners/WebScanner';
import APIScanner from './components/scanners/APIScanner';
import MobileScanner from './components/scanners/MobileScanner';
import SourceCodeScanner from './components/scanners/SourceCodeScanner';
import BlockchainScanner from './components/scanners/BlockchainScanner';
import theme from './theme';

function App() {
  return (
    <ChakraProvider theme={theme}>
      <Router>
        <Box minH="100vh">
          <Navbar />
          <Container maxW="container.xl" py={8}>
            <Routes>
              <Route path="/" element={<WebScanner />} />
              <Route path="/api" element={<APIScanner />} />
              <Route path="/mobile" element={<MobileScanner />} />
              <Route path="/source" element={<SourceCodeScanner />} />
              <Route path="/blockchain" element={<BlockchainScanner />} />
            </Routes>
          </Container>
        </Box>
      </Router>
    </ChakraProvider>
  );
}

export default App;
